:root{
    --cc-primary: #E52926;
    --cc-primary-light: #EE7573;
    --cc-primary-dark: #970200;
    --cc-primary-disabled: #EED1D1;
    --cc-secondary: #F8F8F0;
    --cc-grey-text: #575757;
    --cc-black: #000000;
    --cc-green : #11d60b;
    --cc-secondary-translucent: rgba(248, 248, 240, 0.85);
}

* {
    font-family: 'Silka', sans-serif;
}


/***************************************************************************************************************************************/
/* code for the navigation bar shared by all pages */
/***************************************************************************************************************************************/

.nav-bar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: transparent;
    position: fixed;
    width: 97%;
    height: 60px;
    padding: 0.6% 1% 0.4% 2%;
    transition: background-color 0.3s ease;
}

.nav-bar.scrolled {
    background-color: var(--cc-black);
}

.nav-logo {
    height: 48px;
}

.nav-links-wrapper {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 40vw;
}

.nav-button {
    background-color: transparent;
    border: none;
    font-family: 'Silka', sans-serif;
    font-size: larger;
    margin: 0 8px;
    font-weight: bold;
    transition: color 0.5s ease;
    color: var(--cc-secondary);
}

.nav-a {
    text-decoration: none;
    display: flex;
    align-items: center;
    color: var(--cc-secondary);
}

.nav-a-text, .nav-a-bar {
    font-size: larger;
    color: var(--cc-secondary);
    margin: 0 1vw;
    transition: color 0.5s ease;
}

.nav-a-ig {
    margin-right: 1vw;
    transition: color 0.5s ease;
}

.nav-button:hover, .nav-a:hover, .nav-a:hover .nav-a-text {
    cursor: pointer;
    color: var(--cc-black);
}

.nav-scrolled-button:hover, .nav-scrolled-a:hover .nav-a-text, .nav-scrolled-a:hover .nav-a-ig {
    color: var(--cc-primary);
}


/***************************************************************************************************************************************/
/* code for the intro blocks */
/***************************************************************************************************************************************/


.lp-intro {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: flex-end;
    height: 74vh;
    width: 100%;
    background-color: var(--cc-primary);
}

.lp-intro-text-wrapper {
    width: 80%;
    margin-bottom: 8vh;
}

.lp-intro-text {
    margin: 3vh 0;
    font-size: 300%;
    color: var(--cc-secondary);
}

.lp-intro-cta-wrapper {
    width: 40%;
    min-width: fit-content;
    height: 8%;
    margin-bottom: 4vh;
    display: flex;
    justify-content: space-between;
}

.lp-intro-cta {
    height: 100%;
    width: 40%;
    min-width: fit-content;
    margin: 0 2%;
    padding: 1%;
    color: var(--cc-secondary);
    font-size: x-large;
    background-color: transparent;
    border: 2px solid var(--cc-secondary);
    transition: background-color 1.5s ease;
}

.lp-intro-cta:hover {
    cursor: pointer;
    background-color: var(--cc-primary-dark);
    border: var(--cc-primary-dark);
}

/***************************************************************************************************************************************/
/* code for the shared image blocks */
/***************************************************************************************************************************************/

.image-block {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    height: 80vh;
    min-height: fit-content;
    width: 80%;
    padding: 0 10%;
    background-color: var(--cc-secondary);
}

.image-block-reverse-display{
    flex-direction: row-reverse;
}

.image-block-content-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    height: 60%;
    min-height: fit-content;
    width: 45%;
}

.image-block-title {
    width: 100%;
    margin: 0;
    font-size: 340%;
}

.image-block-text {
    margin-top: 0;
    font-size: x-large;
    text-align: left;
}

.right-align {
    text-align: right;
}

.image-block-button {
    height: 12%;
    min-height: fit-content;
    width: 40%;
    min-width: fit-content;
    color: var(--cc-primary);
    border: 2px solid var(--cc-primary);
    background-color: transparent;
    font-size: x-large;
    transition: background-color 1.5s ease;
}

.image-block-button:hover {
    cursor: pointer;
    background-color: var(--cc-primary);
    color: var(--cc-secondary);
    border: 2px solid var(--cc-primary);
}

.image-block-image-wrapper {
    height: 70%;
    width: 50%;
}

.image-block-image {
    object-fit: cover;
    height: 100%;
    width: 100%;
    border: 3px solid var(--secondary-disabled);
}

/***************************************************************************************************************************************/
/* code for the landing page review block */
/***************************************************************************************************************************************/

.reviews-wrapper {
    width: 80%;
    padding: 0 10%;
    display: flex;
    align-items: center;
    height: 100vh;
    min-height: fit-content;
    background-color: var(--cc-primary);
    flex-direction: column;
    justify-content: center;
}

.review {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    height: 22%;
    min-height: fit-content;
    width: 70%;
    margin: 2%;
    padding: 1% 2%;
    border-radius: 8px;
    box-shadow: 4px 6px 5px rgba(0, 0, 0, 0.25);    
    background-color: var(--cc-secondary);
}

.review-headshot {
    height: 110px;
    width: 110px;
    border-radius: 50%;
    margin-right: 1%;
}

.review-content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    width: fit-content;
}

.review-title {
    font-size: x-large;
    margin: 8px 0;
}

.review-text {
    font-size: large;
    text-align: left;
    margin: 0;
}

/***************************************************************************************************************************************/
/* code for the text-block shared by pages */
/***************************************************************************************************************************************/

.text-block-sec {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: fit-content;
    width: 80%;
    padding: 0 10%;
    background-color: var(--cc-secondary);
    padding-bottom: 10vh;
}

.text-block-title {
    font-size: 250%;
}





/***************************************************************************************************************************************/
/* code for the footer shared by all pages */
/***************************************************************************************************************************************/


.footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    height: 20vh;
    padding: 20px 5vw;
    background-color: var(--cc-primary);

}

.footer-text {
    color: var(--cc-secondary);
    font-size: larger;
}

.ig-icon {
    color: var(--cc-secondary);
}

.ig-icon:hover {
    color: var(--cc-primary-light);
}

.copyright-text {
    color: var(--cc-secondary);
}






















/***************************************************************************************************************************************/
/* code for the student page */
/***************************************************************************************************************************************/


