    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 76vw;
}

.s-block6-text {
    width: 90%;
    font-size: 1.2vw;
    font-weight: normal;
}

.s-block7-wrapper {
    background-image: url(/src/assets/images/cc11.JPEG);
    background-position: center;
